import React from 'react'

export default function Grid (props) {
  const lineWidth = '0.0625rem'
  const doubleLineWidth = '0.125rem'
  const width = props.width || 16
  const height = props.height || 16
  const primaryColor = 'rgba(217, 217, 217, 0.5)'
  const secondaryColor = 'rgba(168, 168, 168, 0.5)'
  const style = {
    opacity: props.visible ? 1 : 0,
    position: 'absolute',
    width: '100%',
    height: '100%',
    top: 0,
    left: 0,
    zIndex: 10,
    userSelect: 'none',
    pointerEvents: 'none',
    backgroundSize: `${width}px ${height}px`,
    backgroundImage: `
    linear-gradient(to bottom, ${primaryColor} ${lineWidth}, transparent ${lineWidth}),
    linear-gradient(to right, ${primaryColor} ${lineWidth}, transparent ${lineWidth}),
    linear-gradient(to bottom, ${secondaryColor} ${doubleLineWidth}, transparent ${lineWidth}),
    linear-gradient(to right, ${secondaryColor} ${doubleLineWidth}, transparent ${lineWidth})
    `
  }
  return <div style={style} />
}



