import React from 'react'

export default function Window (props) {
  const lineWidth = '0.0625rem'
  const width = props.width || 320
  const height = props.height || 240
  const style = {
    opacity: props.visible ? 1 : 0,
    width: width,
    height: height,
    userSelect: 'none',
    pointerEvents: 'none',
    position: 'absolute',
    top: 0,
    left: 0,
    zIndex: 10000,
    outline: `${lineWidth} dashed rgba(0, 0, 0, 0.5)`,
    boxShadow: `0px 0px 0px ${lineWidth} white`
  }
  return <div style={style} />
}



