function clamp (number, min, max) {
  return Math.max(min, Math.min(number, max))
}
export function load () {
  document.title = this.data.project.name
}

export function setZoom (zoom) {
  let editor = this.data.editor
  editor.zoom = clamp(zoom, 0.25, 10)
  this.setState({editor})
}

export function setGrid (width, height) {
  let editor = this.data.editor
  editor.grid = { width, height }
  this.setState({editor})
}

export function showGrid (boolean) {
  let editor = this.data.editor
  editor.preferences.showGrid = boolean
  this.setState({editor})
}

export function updateCursorPosition (x, y) {
  let editor = this.data.editor
  editor.cursor = { x, y }
  this.setState({editor})
}



