import React, { Component } from 'react'
import Theme from '../Theme'
import Button from './Button'

export default class File extends Component {

  change (evt) {
    if (this.props.onChange) this.props.onChange(evt)
  }

  render () {
    return (
      <div>
        <input ref='input' accept='.cop' type='file' style={styles.file} onChange={(evt) => this.change(evt)} />
        <Button
          name={this.props.name || 'fas fa-save'}
          onClick={() => this.refs.input.click()}
        />
      </div>
    )
  }
}

const styles = {
  button: {
    border: 'none',
    outline: 'none',
    width: 48,
    height: 24,
    margin: Theme.MARGIN,
    color: Theme.TEXT,
    borderRadius: Theme.CORNER_RADIUS,
    backgroundColor: Theme.BUTTON
  },
  file: {
    display: 'none'
  }
}



