import React, { Component } from 'react'
import Theme from '../Theme'
import * as ProjectManager from '../project'

export default class Layout extends Component {
  componentWillMount () {
    document.body.style.color = Theme.TEXT
    document.body.style.margin = 0
    document.body.style.fontFamily = 'Roboto, sans-serif'
    document.body.style.backgroundColor = Theme.BACKGROUND
  }

  drop (e) {
    e.stopPropagation()
    e.preventDefault()

    if (e.dataTransfer.items[0].kind === 'file' && e.dataTransfer.items[0].getAsFile().name.endsWith('.cop')) {
      const cop = e.dataTransfer.items[0].getAsFile()
      ProjectManager.load(cop)
    }
  }

  dragOver (e) {
    e.stopPropagation()
    e.preventDefault()
  }

  render () {
    return (
      <div style={styles.layout} onDrop={this.drop} onDragOver={this.dragOver}>
        {this.props.children}
      </div>
    )
  }
}

const styles = {
  layout: {
    display: 'flex',
    flexDirection: 'column',
    height: '100vh',
    zIndex: 1
  }
}



