import React, { Component } from 'react'
import Store from '../Store'

export default class Canvas extends Component {
  onMouseMove (e) {
    const x = e.pageX / this.props.zoom
    const y = e.pageY / this.props.zoom

    const workspaceX = document.body.getBoundingClientRect().left
    const workspaceY = document.body.getBoundingClientRect().top
    const canvasX = this.refs.canvas.getBoundingClientRect().left
    const canvasY = this.refs.canvas.getBoundingClientRect().top

    const cursorX = Math.floor(x + (workspaceX - canvasX))
    const cursorY = Math.floor(y + (workspaceY - canvasY))

    Store.updateCursorPosition(cursorX, cursorY)
  }

  render () {
    const width = this.props.width || 320
    const height = this.props.height || 240
    const backgroundColor = this.props.backgroundColor || 'white'

    const size = { width, height, backgroundColor }
    const canvasStyle = Object.assign({}, styles.canvas, size)

    return <div
      style={Object.assign({}, styles.area, {zoom: this.props.zoom || 1})}
      onMouseMove={this.onMouseMove.bind(this)}
    >
      <div style={styles.margin}>
        <div
          ref='canvas'
          style={canvasStyle}>
          {this.props.children}
        </div>
      </div>
    </div>
  }
}

const styles = {
  area: {
    overflow: 'auto',
    position: 'relative',
    width: '-webkit-fill-available'
  },
  margin: {
    display: 'inline-block',
    width: 'webkit-fill-available',
    margin: 100
  },
  canvas: {
    position: 'relative',
    margin: 0,
    padding: 0
  },
  Viewport: {
    width: 144,
    height: 240,
    outline: '0.0625rem dashed rgba(0, 0, 0, 0.5)',
    boxShadow: '0px 0px 0px 0.065rem white'
  }
}



