import React, { Component } from 'react'
import state from './State'

export default class Modal extends Component {

  componentDidMount () {
    state.link(this)
  }

  close () {
    state.close()
  }

  render () {
    const margedStyles = this.props.style || {}
    margedStyles.minWidth = this.props.minWidth || 'auto'
    margedStyles.minHeight = this.props.minHeight || 'auto'
    return (
      state.visible &&
      <div style={styles.wrap} ref='modal' onClick={this.close.bind(this)} hidden>
        <div style={Object.assign({}, styles.modal, margedStyles)}>
          {state.content}
        </div>
      </div>
    )
  }
}

const styles = {
  wrap: {
    display: 'flex',
    position: 'absolute',
    width: '100vw',
    alignItems: 'center',
    justifyContent: 'center',
    height: '100vh',
    zIndex: 999999,
    background: 'rgba(0, 0, 0, 0.3)',
  },
  modal: {
    color: 'black',
    backgroundColor: 'white',
    borderRadius: 3
  }
}



