import React, { Component } from 'react'
import ObjectSample from './ObjectSample'
import Theme from '../Theme';

export default class ObjectList extends Component {
  render () {
    return (
      <div style={styles.objectListContainer} >
        <ul style={styles.objectListWrapper} >
          { this.props.items.map((o, index) => {
            return (
              <ObjectSample key={index} object={o} />
            )
          })
          }
        </ul>
      </div>
    )
  }
}

const styles = {
  objectListContainer: {
    margin: 0,
    minHeight: '32px',
    height: '160px',
    width: 'auto',
    marginTop: '8px',
    backgroundColor: Theme.BUTTON.DEFAULT,
    overflow: 'scroll',
    borderRadius: Theme.CORNER_RADIUS

  },
  objectListWrapper: {
    listStyleType: 'none',
    margin: 0,
    padding: 0,
    position: 'relative'
  }
}



