import JSZip from 'jszip/lib'

const zip = new JSZip()

export async function load (cop) {
  const data = await zip.loadAsync(cop)
  const project = await data.file('project.json').async('text')
  let p = JSON.parse(project)
  const files = await data.folder('images')
  let f = []

  // const textureId = o.texture
  // const image = new window.Image()
  // image.src = window.URL.createObjectURL(blob)
  // o.texture = image
  files.forEach(async (a, b) => {
    const blob = await b.async('blob')
    f.push(blob)
  })

  console.log(f)
}

