import React, { Component } from 'react'
import styles from './styles'

export default class Transform extends Component {
  constructor () {
    super()

    this.state = {
      id: 0,
      x: 0,
      y: 0,
      width: 0,
      height: 0,
      originalWidth: 0,
      originalHeight: 0,
      originalOriginX: 0,
      originalOriginY: 0,
      angle: 0,
      originX: 0,
      originY: 0,
      grid: { width: 1, height: 1 },
      selected: false
    }

    this.handles = {
      sides: ['top', 'right', 'bottom', 'left'],
      corners: ['topLeft', 'topRight', 'bottomLeft', 'bottomRight']
    }

    this.isDown = false
    this.offset = {x: 0, y: 0}
    this.isResizing = false
    this.previusWidth = 0
    this.previusHeight = 0
    this.previusX = 0
    this.previusY = 0
    this.dx = 0
    this.dy = 0
    this.currentHandle = null

    document.addEventListener('mousemove', this.onMouseMove.bind(this), true)
    document.addEventListener('mouseup', this.onMouseUp.bind(this), true)
  }

  componentDidMount () {
    this.setState({
      id: this.props.id || 0,
      x: this.props.x || 0,
      y: this.props.y || 0,
      width: this.props.width || 64,
      height: this.props.height || 64,
      originalWidth: this.props.width || 64,
      originalHeight: this.props.height || 64,
      originX: this.props.originX || 0,
      originY: this.props.originY || 0,
      originalOriginX: this.props.originX || 0,
      originalOriginY: this.props.originY || 0,
      zoom: this.props.zoom || 1,
      grid: this.props.grid || { width: 1, height: 1 }
    })
  }

  setHandles (fourHandles) {
    return fourHandles ? this.handles.corners
      : this.handles.corners.concat(this.handles.sides)
  }

  setAngle (angle) {
    this.setState({angle})
    this.getComponent().style.transform = `rotate(${angle}deg)`
    this.getComponent().style.transformOrigin = `${this.state.originX}px ${this.state.originY}px`
  }

  select () {
    this.getComponent().style.background = this.props.background || 'rgba(41, 243, 208, 0.2)'
    this.getComponent().style.boxShadow = `0px 0px 0px 0.25rem ${this.props.color || '#00f3d3'}`

    Array.from(this.refs.handles.childNodes).forEach(child => {
      child.style.display = 'block'
    })

    this.setState({selected: true}, () => {
      // onSelect callback
      if (this.props.onSelect) this.props.onSelect(this.state)
    })
  }

  unSelect () {
    this.getComponent().style.background = 'transparent'
    this.getComponent().style.boxShadow = 'none'
    this.setState({selected: false})

    Array.from(this.refs.handles.childNodes).forEach(child => {
      child.style.display = 'none'
    })
  }

  onMouseDown (e) {
    this.isDown = true
    this.offset = {
      x: this.refs.component.offsetLeft - e.pageX / this.state.zoom,
      y: this.refs.component.offsetTop - e.pageY / this.state.zoom
    }
    this.dx = e.pageX
    this.dy = e.pageY
    this.previusWidth = this.state.width
    this.previusHeight = this.state.height
    this.previusX = this.refs.component.offsetLeft
    this.previusY = this.refs.component.offsetTop

    this.refs.component.style.cursor = 'all-scroll'
  }

  onMouseUp () {
    this.isDown = false
    this.isResizing = false
    this.refs.component.style.cursor = 'default'
  }

  setSize (width, height) {
    width = width > 0 ? width : 0
    height = height > 0 ? height : 0
    this.setState({width, height})
    this.getComponent().style.width = `${width}px`
    this.getComponent().style.height = `${height}px`
    this.updateOrigin()
  }

  setPosition (x, y) {
    this.setState({x, y})
    this.getComponent().style.left = `${x - this.state.originX}px`
    this.getComponent().style.top = `${y - this.state.originY}px`
  }

  updateOrigin () {
    const originX = this.state.originalOriginX * this.state.width / this.state.originalWidth
    const originY = this.state.originalOriginY * this.state.height / this.state.originalHeight
    this.setState({originX, originY})
  }

  setGrid (width, height) {
    width = width < 1 ? 1 : width
    height = height < 1 ? 1 : height

    const grid = { width, height }

    this.setState({grid})
  }

  setZoom (zoom) {
    this.setState({zoom})
  }
  getComponent () {
    return this.refs.component
  }

  snap (value, grid) {
    return Math.round(value / grid) * grid
  }
  onMouseMove (e) {
    e.preventDefault()
    if (this.isDown && !this.isResizing) {
      const mouse = {
        x: e.pageX / this.state.zoom,
        y: e.pageY / this.state.zoom

      }
      const nx = mouse.x + (this.offset.x + this.state.originX)
      const ny = mouse.y + (this.offset.y + this.state.originY)

      const gridX = this.snap(nx, this.state.grid.width)
      const gridY = this.snap(ny, this.state.grid.height)

      this.setPosition(gridX, gridY)

      // isDragging callback
      if (this.props.isDragging) this.props.isDragging(this.state)
    }

    if (this.isResizing) {
      if (this.currentHandle === 'top') {
        const y = this.previusY + (e.pageY - this.dy) / this.state.zoom
        const gridY = this.snap(y, this.state.grid.height)
        const gridHeight = this.previusHeight + (this.previusY - gridY)

        this.setSize(this.state.width, gridHeight)
        this.setPosition(this.state.x, gridY)
      } else if (this.currentHandle === 'right') {
        const width = this.previusWidth + (e.pageX - this.dx) / this.state.zoom

        const gridWidth = this.snap(width, this.state.grid.width)

        this.setSize(gridWidth, this.state.height)
      } else if (this.currentHandle === 'bottom') {
        const height = this.previusHeight + (e.pageY - this.dy) / this.state.zoom

        const gridHeight = this.snap(height, this.state.grid.height)
        this.setSize(this.state.width, gridHeight)
      } else if (this.currentHandle === 'left') {
        const x = this.previusX + (e.pageX - this.dx) / this.state.zoom
        const gridX = this.snap(x, this.state.grid.width)
        const gridWidth = this.previusWidth + (this.previusX - gridX)

        this.setPosition(gridX, this.previusY)
        this.setSize(gridWidth, this.state.height)
      } else if (this.currentHandle === 'bottomLeft') {
        const x = this.previusX + (e.pageX - this.dx) / this.state.zoom
        const height = this.previusHeight + (e.pageY - this.dy) / this.state.zoom

        const gridX = this.snap(x, this.state.grid.width)
        const gridWidth = this.previusWidth + (this.previusX - gridX)
        const gridHeight = this.snap(height, this.state.grid.height)

        this.setSize(gridWidth, gridHeight)
        this.setPosition(gridX, this.state.y)
      } else if (this.currentHandle === 'topRight') {
        const y = this.previusY + (e.pageY - this.dy) / this.state.zoom
        const width = this.previusWidth + (e.pageX - this.dx) / this.state.zoom

        const gridY = this.snap(y, this.state.grid.height)
        const gridWidth = this.snap(width, this.state.grid.width)
        const gridHeight = this.previusHeight + (this.previusY - gridY)

        this.setSize(gridWidth, gridHeight)
        this.setPosition(this.state.x, gridY)
      } else if (this.currentHandle === 'topLeft') {
        const x = this.previusX + (e.pageX - this.dx) / this.state.zoom
        const y = this.previusY + (e.pageY - this.dy) / this.state.zoom

        const gridX = this.snap(x, this.state.grid.width)
        const gridY = this.snap(y, this.state.grid.height)
        const gridWidth = this.previusWidth + (this.previusX - gridX)
        const gridHeight = this.previusHeight + (this.previusY - gridY)

        this.setSize(gridWidth, gridHeight)
        this.setPosition(gridX, gridY)
      } else if (this.currentHandle === 'bottomRight') {
        const width = this.previusWidth + (e.pageX - this.dx) / this.state.zoom
        const height = this.previusHeight + (e.pageY - this.dy) / this.state.zoom

        const gridWidth = this.snap(width, this.state.grid.width)
        const gridHeight = this.snap(height, this.state.grid.height)

        this.setSize(gridWidth, gridHeight)
      }

      // onResize callback
      if (this.props.onResize) this.props.onResize(this.state)
    }
  }

  onResize (handle, e) {
    e.preventDefault()
    this.isResizing = true
    this.currentHandle = handle
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.zoom && nextProps.zoom !== this.state.zoom) {
      this.setZoom(nextProps.zoom)
    }

    if (nextProps.grid && nextProps.grid !== this.state.grid) {
      this.setGrid(nextProps.grid.width, nextProps.grid.height)
    }
  }

  render () {
    let style = {
      left: this.state.x - this.state.originX,
      top: this.state.y - this.state.originY,
      width: this.state.width,
      height: this.state.height
    }

    let originStyle = {
      left: this.state.originX - 1,
      top: this.state.originY - 1,
      backgroundColor: this.props.color || '#3bf3d3'
    }

    let handleStyle = {
      backgroundColor: this.props.color || '#3bf3d3'

    }

    style = Object.assign({}, style, styles.transform, this.props.style)
    originStyle = Object.assign({}, originStyle, styles.origin, this.props.originStyle)
    handleStyle = Object.assign({}, handleStyle, styles.handle, this.props.handleStyle)
    const handles = this.setHandles(this.props.fourHandles)

    return (
      <div ref='component'
        style={style}
        tabIndex='1'
        onFocus={this.select.bind(this)}
        onBlur={this.unSelect.bind(this)}
        onMouseDown={this.onMouseDown.bind(this)}
      >
        <div ref='handles'>
          {handles.map((handle, index) => {
            return <div
              ref={handle}
              key={index}
              onMouseDown={e => this.onResize.call(this, handle, e)}
              style={Object.assign({}, handleStyle, styles[handle])}
              position={handle}
            />
          })}
          <div ref='origin' style={originStyle} />
        </div>
        <div style={styles.element}>
          {this.props.children}
        </div>
      </div>
    )
  }
}



