import React from 'react'

export default function Sprite (props) {
  const style = {
    background: `url(${props.source})`,
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundSize: '100% 100%',
    imageRendering: props.sampling
  }

  return <div style={style} />
}



