import React from 'react'
import Theme from '../Theme'
import Store from '../Store'

export default function StatusBar () {
  return (
    <div style={styles.bar}>

      <i className='fas fa-file-archive' style={styles.icon} />
      <span style={styles.label}>{Store.data.project.name}.cop</span>

      <i className='fas fa-image' style={styles.icon} />
      <span style={styles.label}>{Store.data.editor.currentStage}</span>

      <span style={styles.label}>
        Mouse: {Store.data.editor.cursor.x}, {Store.data.editor.cursor.y}
      </span>

      <i className='fa fa-search-plus' style={styles.icon} />
      <span style={styles.label}>{Store.data.editor.zoom * 100 }%</span>
    </div>
  )
}

const styles = {
  bar: {
    zIndex: 1,
    display: 'flex',
    height: 24,
    fontSize: 16,
    alignItems: 'center',
    paddingLeft: 16,
    paddingRight: 16,
    margin: Theme.MARGIN,
    userSelect: 'none',
    color: Theme.TEXT.LIGHT,
    backgroundColor: '#ef6c00'
    
  },
  icon: {
    fontSize: 14
  },
  label: {
    fontSize: 11,
    paddingLeft: 8,
    paddingRight: 32
  }
}



