import React, { Component } from 'react'
import Theme from '../Theme'

export default class Button extends Component {

  state = {
    disabled: false
  }

  click () {
    if (this.props.onClick) this.props.onClick()
  }

  hover () {
    // this.refs.button.style.width = '56px'
    this.refs.button.style.backgroundColor = Theme.BUTTON.ACTIVE
  }

  leave () {
    // this.refs.button.style.width = '48px'
    this.refs.button.style.backgroundColor = Theme.BUTTON.DEFAULT
  }

  press () {
    this.refs.button.style.backgroundColor = Theme.BUTTON.PRESSED
  }

  render () {

    const style = {
      color: this.props.disabled ? Theme.TEXT.DARK : Theme.TEXT.LIGHT,
      fontSize: this.props.size || 16
    }

    return (
      <button
        ref='button'
        onMouseEnter={this.hover.bind(this)}
        onMouseLeave={this.leave.bind(this)}
        onMouseDown={this.press.bind(this)}
        onMouseUp={this.hover.bind(this)}
        style={Object.assign({}, styles.button, style)}
        onClick={this.click.bind(this)}
        title={this.props.title}
      >
        <i className={this.props.name} />
      </button>
    )
  }
}

const styles = {
  button: {
    width: 48,
    height: 24,
    border: 'none',
    outline: 'none',
    cursor: 'pointer',
    color: Theme.TEXT.LIGHT,
    margin: Theme.MARGIN,
    borderRadius: Theme.CORNER_RADIUS,
    backgroundColor: Theme.BUTTON.DEFAULT,
    transitionDuration: '200ms'

  }
}



