import React, { Component } from 'react'

export default class Workspace extends Component {
  render () {
    return (
      <div style={styles.layout}>
        {this.props.children}
      </div>
    )
  }
}

const styles = {
  layout: {
    display: 'flex',
    justifyContent: 'space-between',
    height: '100vh'
  }
}



