import React, { Component } from 'react'
import Theme from '../Theme';

export default class Section extends Component {

  state = { expanded: this.props.expanded }

  toggleSection () {
    this.setState({ expanded: !this.state.expanded })
  }

  render () {
    // <span style = { { fontSize: "10px", color: "#cccccc", textAlign: "center", display: "block" }} > Sección vacía </span>
    return (
      <div style={styles.section}>

        <div style={styles.sectionHeader} onClick={this.toggleSection.bind(this)} >
          <div style={Object.assign({ transform: this.state.expanded ? 'rotate(90deg)' : 'rotate(0deg)' }, styles.sectionCaret)} />
          <span style={styles.sectionTitle}> {this.props.title} </span>
        </div>

        <div style={Object.assign({ height: this.state.expanded ? 'auto' : '0px' }, styles.content)} >

          { this.props.children }

        </div>
      </div>
    )
  }
}

const styles = {
  section: {
    margin: 'auto',
    width: 'auto',
    height: 'auto',
    minHeight: '32px',
    lineHeight: '36px',
    overflow: 'hidden',
    paddingLeft: 8,
    paddingRight: 8
  },
  sectionTitle: {
    paddingLeft: '3px',
    fontSize: '12px',
    color: Theme.TEXT.DARK,
    userSelect: 'none',
    textTransform: 'capitalize'
  },
  sectionCaret: {
    position: 'relative',
    float: 'left',
    top: '10px',
    width: 0,
    height: 0,
    margin: '4px',
    borderTop: '5px solid transparent',
    borderLeft: '8px solid #666',
    borderBottom: '5px solid transparent',
    transitionDuration: '100ms'
  },

  sectionHeader: {
    cursor: 'pointer'
  },
  content: {
    overflow: 'hidden',
    transition: 'height 300ms'
  }
}



