import React, { Component } from 'react'
import Workspace from './components/Workspace'
import TopBar from './components/TopBar'
import Panel from './components/Panel'
import Settings from './components/Settings'
import Brand from './components/Brand'
import Project from './components/Project'
import Store from './Store'
import Layout from './components/Layout'
import Canvas from './components/Canvas'
import StatusBar from './components/StatusBar'
import Modal from './components/modal'
import Section from './components/Section'
import ObjectList from './components/ObjectList'
import SearchBox from './components/SearchBox'
import Transform from './components/react-transform'
import Grid from './components/Grid'
import Screen from './components/Screen'
import Sprite from './components/Sprite'
import texture from './assets/mauricio.png'
import background from './assets/background.png'
import Background from './components/Background';

export default class App extends Component {
  componentWillMount () {
    Store.link(this)
    Store.load()
  }

  render () {
    return (
      <Layout>
        <TopBar>
          <Brand />
          <Project />
          <Settings />
        </TopBar>
        <Workspace>
          <Panel>
            <Section title='Palette' expanded>
              <SearchBox placeholder='Search' />
              <ObjectList items={[{ type: 'Sprite', icon: 'fa fa-image' }, { type: 'Background', icon: 'fab fa-delicious' }, { type: 'Text', icon: 'fa fa-font' }]} />
            </Section>
            <Section title='Objects' />
          </Panel>
          <Canvas
            width={Store.data.project.stages[0].width}
            height={Store.data.project.stages[0].height}
            zoom={Store.data.editor.zoom}>
            <Screen
              width={Store.data.project.width}
              height={Store.data.project.height}
              visible={Store.data.editor.preferences.showScreen}
            />
            <Grid
              width={Store.data.editor.grid.width}
              height={Store.data.editor.grid.height}
              visible={Store.data.editor.preferences.showGrid}
            />

            <Transform {...{
              id: 2,
              width: 64,
              height: 64,
              grid: Store.data.editor.grid,
              zoom: Store.data.editor.zoom
            }}>
              <Background
                source={background}
                sampling={Store.data.project.imageRendering} />
            </Transform>
            <Transform {...{
              id: 1,
              width: 24,
              height: 32,
              grid: Store.data.editor.grid,
              zoom: Store.data.editor.zoom
            }}>
              <Sprite
                source={texture}
                sampling={Store.data.project.imageRendering} />
            </Transform>
          </Canvas>
          <Panel>
            <Section title='Properties' />
            <Section title='Info' />
          </Panel>
        </Workspace>
        <Modal minWidth={320} minHeight={80} />
        <StatusBar />
      </Layout>
    )
  }
}



