import React from 'react'

export default function Background (props) {
  let repeat = 'repeat'

  if (!props.repeatX && props.repeatY) repeat = 'repeat-y'
  else if (props.repeatX && !props.repeatY) repeat = 'repeat-x'

  const style = {
    background: `url(${props.source}) ${repeat}`,
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    imageRendering: props.sampling
  }

  return <div style={style} />
}



