import React from 'react'
import Button from './Button'
import ButtonGroup from './ButtonGroup'
import File from './File'
import Store from '../Store'
import * as ProjectManager from '../project'

export default class Project extends React.Component {

  loadProject (evt) {
    if (evt.target.files) {
      const file = evt.target.files[0]
      ProjectManager.load(file)
    }
  }

  saveProject () {

  }

  zoomIn () {
    const zoom = Store.data.editor.zoom + 0.25
    Store.setZoom(zoom)
  }

  zoomOut () {
    const zoom = Store.data.editor.zoom - 0.25
    Store.setZoom(zoom)
  }

  zoomReset () {
    Store.setZoom(1)
  }

  toggleGrid () {
    Store.showGrid(!Store.data.editor.preferences.showGrid)
  }

  render () {
    return (
      <ButtonGroup>
        <Button name='fas fa-file' onClick={null} />
        <File name='fas fa-folder-open' onChange={this.loadProject} />
        <Button name='fas fa-save' onClick={this.saveProject} />
        <Button name='fa fa-undo' onClick={this.saveProject} />
        <Button name='fa fa-redo' onClick={this.saveProject} />
        <Button name='fas fa-th' title='Toggle grid' onClick={this.toggleGrid} disabled={!Store.data.editor.preferences.showGrid} />
        <Button name='fa fa-search-plus' onClick={this.zoomIn} />
        <Button name='fa fa-expand' onClick={this.zoomReset} />
        <Button name='fa fa-search-minus' onClick={this.zoomOut} />
        <Button name='fa fa-play' />
        <Button name='fa fa-share' />
      </ButtonGroup>
    )
  }
}



