export default {
  visible: false,
  content: null,
  modal: null,
  link (modal) {
    this.modal = modal
  },
  show (body) {
    this.visible = true
    this.content = body
    if (this.modal) {
      this.modal.forceUpdate()
    }
  },
  close () {
    this.visible = false
    this.content = null
    if (this.modal) {
      this.modal.forceUpdate()
    }
  }
}



