import React from 'react'
import Button from './Button'
import ButtonGroup from './ButtonGroup'
import { ModalOptions } from './modal'

export default class Settings extends React.Component {

  click () {
    ModalOptions.show(<p>Perro</p>)
  }

  click2 () {
    ModalOptions.show(<p>CARAJA</p>)
  }
  
  render () {
    return (
      <ButtonGroup>
        <Button name='fab fa-android' />
        <Button name='fab fa-apple' />
        <Button name='fab fa-facebook-messenger' onClick={this.click2} />
        <Button name='fa fa-cog' onClick={this.click} />
      </ButtonGroup>
    )
  }
}



