import Mafuba from '../Mafuba'
import * as Mutations from './Mutations'

export default new Mafuba({
  data: {
    editor: {
      zoom: 1,
      currentStage: 'Main',
      cursor: { x: 0, y: 0 },
      grid: { width: 16, height: 16 },
      preferences: {
        showGrid: false,
        showScreen: true
      }
    },
    project: {
      name: 'New project',
      width: 144,
      height: 256,
      imageRendering: 'pixelated',
      objects: [{
        id: 1,
        type: 'Sprite',
        name: 'Bird',
        width: 24,
        height: 24,
        texturePath: '../images/1.png',
        behaviors: []
      },
      {
        id: 1,
        type: 'Background',
        name: 'Background',
        width: 144,
        height: 256,
        texturePath: '../images/0.png',
        behaviors: []
      }],
      stages: [
        {
          name: 'Default',
          width: 144,
          height: 256,
          backgroundColor: '#ffffff',
          instances: [{
            id: 1,
            objectId: 1,
            x: 20,
            y: 100,
            width: 16,
            height: 16,
            opacity: 1,
            angle: 0
          },
          {
            id: 2,
            objectId: 2,
            x: 0,
            y: 0,
            width: 144,
            height: 256,
            opacity: 1,
            angle: 0
          }]
        }
      ]
    }
  },
  mutations: Mutations
})



