import React, { Component } from 'react'
import store from '../Store'
import Theme from '../Theme';

export default class SearchBox extends Component {
  filter (e) {
    store.editor.objectFilter = e.target.value
  }
  render () {
    return (
      <input type='Text' style={styles.input} spellCheck='false' placeholder={this.props.placeholder} onChange={this.filter} />
    )
  }
}

const styles = {
  input: {
    margin: 0,
    border: 0,
    borderRadius: Theme.CORNER_RADIUS,
    display: 'block',
    width: '-webkit-fill-available',
    height: '4px',
    backgroundColor: Theme.BUTTON.DEFAULT,
    color: '#aaa',
    padding: '10px',
    fontSize: '14px',
    outline: 'none',
    textTransform: 'capitalize'
  }
}



