import React, { Component } from 'react'
// import MaterialIcon from '../MaterialIcon/index'

export default class ObjectSample extends Component {
  constructor (props) {
    super(props)
    this.dragStart = this.dragStart.bind(this)
    this.state = this.props.object
  }

  dragStart (ev) {
    ev.dataTransfer.setData('object', JSON.stringify(this.state))
  }

  render () {
    return (
      <li style={styles.objectListItem} draggable onDragStart={this.dragStart} onDrag={null} >
        <i className={this.props.object.icon} style={styles.icon} />
        <span style={styles.objectName} > { this.props.object.type } </span>
      </li>
    )
  }
}

const styles = {
  objectListItem: {
    color: 'white',
    paddingLeft: '8px',
    textDecoration: 'none',
    display: 'flex',
    height: ' 32px',
    alignItems: 'center'

  },
  objectName: {
    paddingLeft: '8px',
    paddingRight: '8px',
    whiteSpace: 'nowrap',
    fontSize: '14px'
  },
  icon: {
    fontSize: 16
  }
}



