const sideHandleOffset = '-0.85rem'
const cornerHandleOffset = '-0.85rem'
const handleFit = '0.2rem'

export default {
  transform: {
    position: 'absolute',
    borderRadius: '0.24rem',
    userSelect: 'none',
    outline: 'none',
    zIndex: 1
  },
  element: {
    overflow: 'hidden',
    width: '100%',
    height: '100%'
  },
  origin: {
    position: 'absolute',
    width: '0.1875rem',
    height: '0.1875rem',
    display: 'none'
  },
  handle: {
    position: 'absolute',
    width: '0.5rem',
    height: '0.5rem',
    borderRadius: '0.125rem',
    display: 'none',
    // boxShadow: '0 0 0 0.0625rem #1c928c'
  },
  topLeft: {
    left: cornerHandleOffset,
    top: cornerHandleOffset,
    cursor: 'nwse-resize'
  },
  topRight: {
    right: cornerHandleOffset,
    top: cornerHandleOffset,
    cursor: 'nesw-resize'
  },
  bottomLeft: {
    left: cornerHandleOffset,
    bottom: cornerHandleOffset,
    cursor: 'nesw-resize'
  },
  bottomRight: {
    right: cornerHandleOffset,
    bottom: cornerHandleOffset,
    cursor: 'nwse-resize'
  },
  top: {
    width: '0.3375rem',
    height: '0.3375rem',
    left: `calc(50% - ${handleFit})`,
    top: sideHandleOffset,
    cursor: 'ns-resize'
  },
  right: {
    width: '0.3375rem',
    height: '0.3375rem',
    right: sideHandleOffset,
    bottom: `calc(50% - ${handleFit})`,
    cursor: 'ew-resize'
  },
  bottom: {
    width: '0.3375rem',
    height: '0.3375rem',
    right: `calc(50% - ${handleFit})`,
    bottom: sideHandleOffset,
    cursor: 'ns-resize'
  },
  left: {
    width: '0.3375rem',
    height: '0.3375rem',
    left: sideHandleOffset,
    bottom: `calc(50% - ${handleFit})`,
    cursor: 'ew-resize'
  }
}



